    .active, .next, .prev {
      display: block;
    }

    .active {
      left: 0;
    }

    .next, .prev {
      position: absolute;
      top: 0;
      width: 100%;
    }

    .next {
      left: 100%;
    }

    .prev {
      left: -100%;
    }

    .next.left, .prev.right {
      left: 0;
    }

    .active {
      &.left {
        left: -100%;
      }

      &.right {
        left: 100%;
      }
    }
  }
}

